import React, {Component} from 'react'
import MediaQuery from 'react-responsive'

const titles = [
  /* 0 */ 'Hq',
  /* 1 */ 'Mission',
  /* 2 */ 'Context',
  /* 3 */ 'Science',
  /* 4 */ 'Projects',
  /* 5 */ 'Team',
  /* 6 */ 'Contact'
];

const Logo = (props) => (
  <a className={"navbar-brand mb-0 h1 logo-like shrink-animation logo-" + (titles[props.active] || titles[0]).toLowerCase()} onClick={() => props.changeFullpageSlide(0)}
     id="logo">
    <span><span className="first-letter">H</span>umante</span><span className="last-letter">q</span>
  </a>
)

const NavItem = (props) => (
  <li className={`nav-item ${props.active === props.index ? 'active' : ''}`}
      onClick={() => props.changeFullpageSlide(props.index)}>
    <a className="nav-link">{props.children}</a>
  </li>
)

class NavBar extends Component {
  state = {
    isMenuOpened: false
  }

  onNavItemClick = (index) => {
    const {changeFullpageSlide} = this.props
    changeFullpageSlide && changeFullpageSlide(index)
    this.setState({isMenuOpened: false})
  }
  
  render() {
    const {props} = this
    const {isMenuOpened} = this.state

    let startMenuAt = 1;
    try {
      if (window.innerWidth < 1024) {
        startMenuAt = 0;
      }
    } catch (nextjsServer) {
      // oops
    }
    
    return (
      <header className="navbar navbar-expand navbar-dark flex-column flex-md-row top-bar hq-navbar">
        <div className="navbar-container row grid-x grid-margin-x">
        <div className="title-bar">
          <div className="title-bar-title">
            <Logo {...props}/>
          </div>
        </div>
        </div>
      </header>
    )
  }
}

export default NavBar



